.sombra-curva {
  position: relative; }
  .sombra-curva:after {
    content: "";
    position: absolute;
    width: 96%;
    height: 10%;
    bottom: -1%;
    left: 0;
    z-index: -1;
    border-radius: 50%;
    background-color: #000;
    opacity: .3;
    box-shadow: 0 5px 12px #000; }

.sombra-concava {
  position: relative; }
  .sombra-concava:after, .sombra-concava:before {
    content: "";
    position: absolute;
    width: 96%;
    height: 5%;
    bottom: 3%;
    z-index: -1;
    background-color: #000;
    box-shadow: 0 5px 12px black; }
  .sombra-concava:before {
    left: 1%;
    transform: rotate(-2deg); }
  .sombra-concava:after {
    right: 1%;
    transform: rotate(2deg); }

