.modal {
  position: fixed;
  display: flex;
  flex-wrap: wrap;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  transform: scale3d(0, 0, 0);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url("/img/valparaiso.jpg");
  overflow-y: auto; }
  .modal::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 180vh;
    background-color: #302f2f;
    opacity: .7; }
    @media screen and (min-width: 360px) {
      .modal::before {
        height: 140vh; } }
    @media screen and (min-width: 411px) {
      .modal::before {
        height: 135vh; } }
    @media screen and (min-width: 480px) {
      .modal::before {
        height: 100%; } }
  .modal.zoom-in {
    animation: zoomIn .5s forwards; }

@keyframes zoomIn {
  to {
    transform: scale3d(1, 1, 1); } }
  .modal.zoom-out {
    animation: zoomOut .5s forwards; }

@keyframes zoomOut {
  from {
    transform: scale3d(1, 1, 1); }
  to {
    transform: scale3d(0, 0, 0); } }
  .modal-content {
    position: relative;
    width: 95%;
    margin: auto; }
    @media screen and (min-width: 1024px) {
      .modal-content {
        width: 85%; } }
    @media screen and (min-width: 1400px) {
      .modal-content {
        width: 95%; } }
